import React from "react";

function ProgressBar(props) {

    const {name, percent,inProgress} = props;
    let round = inProgress === true ? "py-1 px-2 uppercase rounded-full bg-pink-200" : "";
    // const colors = ["pink", "red","blue","green"];

  return (
    <div className="relative pt-1">
      <div className="flex mb-2 items-center justify-between">
        <div>
          <span className="text-xs font-semibold inline-block text-pink-600">
            {name}
          </span>
        </div>
        <div className="text-right">
          <span className={`text-xs font-semibold inline-block text-pink-600 ${round}`}>
            {inProgress === false ? `${percent}%` : "IN PROGRESS"}
          </span>
        </div>
      </div>
      <div className="overflow-hidden h-2 mb-4 text-xs flex rounded bg-pink-200">
        <div
          style={{ width: `${percent}%` }}
          className="shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center bg-pink-500"
        ></div>
      </div>
    </div>
  );
}

export default ProgressBar;
