import React from 'react';
import ProgressBar from 'components/ProgressBar';
import state from "./skills.json";

function Skills() {

    const skills = state.map((skill, id) => (
        <ProgressBar key={id} name={skill.name} percent={skill.percentage} inProgress={skill.inProgress} />
    ));

    return ( 
        <div className="mx-6 md:w-2/3 md:mx-auto" id="skills">
            {skills}
        </div>
     );
}

export default Skills;